import React from 'react';
import { Link } from "react-router-dom";
import Line from '../share-compoments/Line';
import servicedata from '../../variables/servicedata';
import Button from '../share-compoments/Button';

function Services() {

    const data = servicedata;
    const content = data.map((item, index) => {
        const list = item.list.map((subitem, index) => {
            return (
                <li className="text-center" key={index}>
                    <p className="d-inline-block text-center">
                        <span className="mr-1">
                            <img alt="in hộp giấy" src="data:image/png;base64," />
                        </span> {subitem}
                    </p>
                </li>
            )
        });
        return (
            <div className="col-sm-12 col-md-6 col-lg-4" key={index}>
                <h4 className="text-center mb-sm-1 mb-md-3 mb-lg-5">{item.title}</h4>
                <ul>
                    {list}
                </ul>
            </div>
        )
    });

    return (
        <div className="bg-light py-2- py-md-4 py-lg-5">
            <div className="container py-2- py-md-4 py-lg-5">
                <div className="row">
                    <h3 className="text-center m-auto mb-sm-1 mb-md-3 mb-lg-5">Cung cấp giải pháp hiệu quả <br />cho sản xuất và dịch vụ của doanh nghiệp</h3>
                    <Line />
                </div>
                <div className="row">
                    {content}
                </div>
                <div className="text-center">
                    <Link to="/product"><Button text="danh mục sản phẩm" /></Link>
                </div>
            </div>
        </div>
    );
}

export default Services;